import { Validators } from '@angular/forms';
import { CommonFormConstructor } from '../../../core/modules/form/common-form-constructor';

const ChangePasswordFormValidation = {
  currentPassword: [Validators.required, Validators.minLength(7)],
  newPassword: [Validators.required],
  confirmNewPassword: [Validators.required]
};

const ChangePasswordFormValidationMessage = {
  'confirmNewPassword.passwordNotEqual': 'Confirmed password not equal to password field'
};

export class ChangePasswordFormModel extends CommonFormConstructor {
  currentPassword = '';
  newPassword = '';
  confirmNewPassword = '';

  constructor(data?: any) {
    super();
    this.setData(data);
  }

  get validationRules() {
    return ChangePasswordFormValidation;
  }

  get validationMessage() {
    return ChangePasswordFormValidationMessage;
  }
}
