import { Component, OnInit, ChangeDetectorRef, ViewChild, ElementRef} from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormControl, Validators, FormGroup } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import {FormService} from "../../../../core/modules/form/form-service.service";
import { UserManagementService } from '../../services/user-management.service';
import { ResetPasswordFormModel } from '../../models/reset-password-form.model';
import { ResetPasswordViewModel } from '../../models/reset-password-view.model';

@Component({
  selector: 'app-reset-password',
  templateUrl: './reset-password.component.html',
  styleUrls: ['./reset-password.component.scss']
})
export class ResetPasswordComponent implements OnInit {
  @ViewChild('popOver') popOver: ElementRef;

  public inputType: string = 'password';

  public tokenValidators = [
    Validators.required,
    Validators.pattern(/\d{6}/)
  ]

  //public passwordValidators = [
  //  Validators.required,
  //  Validators.minLength(8),
  //  // Validators.pattern('(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).*'),
  //  Validators.pattern('(?=.*[a-z]).*'),
  //  Validators.pattern('(?=.*[A-Z]).*'),
  //  Validators.pattern('(?=.*[0-9]).*'),
  //];
  userId: string;
  code: string = '';
  msg: string;

  tokenControl: FormControl;
  tokenValidated: boolean = false;

  resetPasswordForm: FormGroup;
  passwordReset: boolean;


  confirmPasswordInputConditionConfig = [
    { condition: true, validatorText: 'match' },
    { condition: false, validatorText: 'no match' }
  ];
  passwordInputConditionConfig = [
    { condition: [Validators.maxLength(0)], validatorText: 'not strong' },
    { condition: [Validators.maxLength(15)], validatorText: 'strong' },
    { condition: [Validators.maxLength(25)], validatorText: 'super strong' }
  ];

  public toggleInputType(): void {
    this.inputType = (this.inputType === 'password') ? 'text' : 'password';
  }

  constructor(
    private userService: UserManagementService,
    private activatedRoute: ActivatedRoute,
    private cdRef: ChangeDetectorRef,
    private toastr: ToastrService,
    private formService: FormService
  ) {
  }

  ngOnInit() {
    this.tokenControl = new FormControl('', Validators.compose(this.tokenValidators));
    //this.tokenControl.valueChanges.subscribe(value => console.log(value));

    this.resetPasswordForm = this.formService.getFormByModel(new ResetPasswordFormModel());

    this.activatedRoute.queryParams.subscribe(params => {
      this.userId = params['id'];
      this.code = params['code']; 
    });
    console.log(this);
  }

  validateToken() {
    console.log(this.userId, this.code);
    console.log(this.tokenControl.value);

    const token = this.tokenControl.value;
    this.userService.confirmTwoFactorAuthentication(this.userId, token)
      .subscribe((res: any) => {
        if (res.success) {
          // this.toastr.success(res.message);
          this.tokenValidated = true;
          if(!this.cdRef['destroyed']) this.cdRef.detectChanges();
        }
        else {
          this.toastr.error(res.message);
        }
      });
  }

  resetPassword() {
    console.log(this.resetPasswordForm.value);
    let model = new ResetPasswordViewModel();
    model.userId = this.userId;
    model.password = this.resetPasswordForm.get('password').value;
    model.confirmPassword = this.resetPasswordForm.get('confirmPassword').value;

    this.userService.resetPassword(model, this.code).subscribe(result => {
      if (result.success) {
        this.passwordReset = true;
        if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
      }
      else {
        this.toastr.error(result.message);
      }
    });
  }

  public onInputChange(event, popOver): void {
    if (event.target.value && !popOver.isOpen()) {
      popOver.open();
      return;
    } else if (!event.target.value && popOver.isOpen()) {
      popOver.close();
      return;
    }
  }

  public isFieldValid(type): boolean {
    const fieldName = (type === 'minlength') ? type : `${type}Validator`
    return (this.resetPasswordForm.get('password').errors && this.resetPasswordForm.get('password').errors[fieldName]) 
      ? false
      : true;
  }
}
