import { Component, OnInit, ChangeDetectorRef } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';
import { ToastrService } from 'ngx-toastr';
import * as _ from 'lodash';

import {AuthenticationMethodEnum} from "../../../../shared/enums/authMethodEnum";
import { AccountRecoveryViewModel } from '../../models/account-recovery-view.model';
import { AccountRecoveryOptionsModel } from '../../models/account-recovery-options.model';
import { ForgotPasswordViewModel } from '../../models/forgot-password-view.model';
import { UserManagementService } from '../../services/user-management.service';

@Component({
  selector: 'app-acccount-recovery',
  templateUrl: './account-recovery.component.html',
  styleUrls: ['./account-recovery.component.scss']
})
export class AccountRecoveryComponent implements OnInit {
  msg: string;
  recoveryControl: FormControl;
  recoveryMethod: FormControl;

  optionsLoaded: boolean = false;
  recoveryOptions: string[];
  recoveryOptionsModel: AccountRecoveryOptionsModel;

  tokenSent: boolean = false;

  methodDescriptions = {
    email: 'Primary email',
    email2: 'Secondary email',
    phone: 'Phone number'
  };

  constructor(
    private userService: UserManagementService,
    private activatedRoute: ActivatedRoute,
    private cdRef: ChangeDetectorRef,
    private toastr: ToastrService
  ) {}

  ngOnInit() {
    this.recoveryControl = new FormControl(null, Validators.required);
    this.recoveryMethod = new FormControl(null, Validators.required);
    this.recoveryMethod.valueChanges.subscribe(v => console.log(v));

    this.activatedRoute.queryParams.subscribe(params => {
      //this.userId = params['id'];
      //this.code = params['code'];
    });
  }

  getRecoveryOptions(data: AccountRecoveryOptionsModel) {
    return Object.keys(data).filter(k => data[k]);
  }

  getOptions() {
    const model = new AccountRecoveryViewModel();
    const recoveryParam = this.recoveryControl.value;

    if (recoveryParam.includes('@')) {
      model.email = recoveryParam;
    } else {
      model.username = recoveryParam;
    }

    this.userService.recoveryOptions(model).subscribe((res: AccountRecoveryOptionsModel) => {
      console.log(res);
      if (res.success) {
        this.recoveryOptionsModel = _.cloneDeep(res);
        delete res['success'] && delete res['id'];
        this.recoveryOptions = this.getRecoveryOptions(res);
        this.optionsLoaded = true;
        if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
      } else {
        this.toastr.error('Could not verify your username or email address');
      }
    });
  }

  getAuthMethod(type: string): number {
    switch (type) {
      case 'email':
        return AuthenticationMethodEnum.EMAIL;
      case 'email2':
        return AuthenticationMethodEnum.EMAIL2;
      case 'phone':
        return AuthenticationMethodEnum.PHONE;
      default:
        return 0;
    }
  }

  submitForm() {
    console.log(this.recoveryOptionsModel);
    let model = new ForgotPasswordViewModel();
    model.id = this.recoveryOptionsModel.id;
    model.method = this.getAuthMethod(this.recoveryMethod.value);
    this.userService.forgotPassword(model).subscribe(res => {
      console.log(res);
      this.tokenSent = true;
      if (!this.cdRef['destroyed']) this.cdRef.detectChanges();
    });
  }
}
