import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { FormGroup } from '@angular/forms';
import { FormService } from '../../../../core/modules/form/form-service.service';
import { UserManagementService } from '../../services/user-management.service';
import { ChangePasswordFormModel } from '../../models/change-password-form';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html'
})
export class ChangePasswordComponent implements OnInit {
  changePasswordFormModel: ChangePasswordFormModel;
  changePasswordForm: FormGroup;
  @Output() onSubmit: EventEmitter<any> = new EventEmitter();
  @Input() data: any;

  constructor(
    private userManagementService: UserManagementService,
    private formService: FormService
  ) {}

  ngOnInit() {
    this.changePasswordFormModel = new ChangePasswordFormModel(this.data);
    this.changePasswordForm = this.formService.getFormByModel(this.changePasswordFormModel);
    this.changePasswordForm.setValidators(this.checkPasswords);
  }

  saveChangePasswordForm() {
    const saveData = {
      currentPassword: this.changePasswordForm.value.currentPassword,
      newPassword: this.changePasswordForm.value.newPassword,
      confirmNewPassword: this.changePasswordForm.value.confirmNewPassword
    };

    this.onSubmit.emit(saveData);
  }

  checkPasswords(group: FormGroup) {
    // here we have the 'passwords' group
    let pass = group.controls.newPassword.value;
    let confirmPass = group.controls.confirmNewPassword.value;

    group
      .get('confirmNewPassword')
      .setErrors(pass === confirmPass ? null : { passwordNotEqual: true });
    return group.errors;
  }
}
