import {NgModule} from '@angular/core';
import {CommonModule} from '@angular/common';
import {FormsModule, ReactiveFormsModule} from '@angular/forms';

import {
  MatButtonModule, MatCardModule, MatCheckboxModule, MatChipsModule, MatDatepickerModule, MatExpansionModule,
  MatFormFieldModule, MatIconModule, MatInputModule, MatMenuModule, MatPaginatorModule, MatSelectModule,
  MatSortModule, MatStepperModule, MatTableModule, MatTabsModule, MatRadioModule
} from '@angular/material';

import {NgbTabsetModule, NgbPopoverModule, NgbAlertModule} from "@ng-bootstrap/ng-bootstrap";

import {LoaderModule} from '../../shared/components/loader/loader.module';
import {FormModule} from '../../core/modules/form/form.module';
import {CoreModule} from "../../core/core.module";

import {UserManagementService} from './services/user-management.service';
import {UserManagementComponent} from './user-management.component';
import {UserManagementRoutingModule} from './user-management-routing.module';
import {UserAccountComponent} from "./pages/user-account/user-account.component";
import {ChangePasswordComponent} from "./components/change-password/change-password.component";
import {PersonalInfoComponent} from "./components/personal-info/personal-info.component";
import {ConfirmRegistrationComponent} from './pages/confirm-registration/confirm-registration.component';
import {AccountRecoveryComponent} from './pages/account-recovery/account-recovery.component';
import {ResetPasswordComponent} from './pages/reset-password/reset-password.component';


const MaterialModules = [
  MatButtonModule, MatPaginatorModule, MatTableModule, MatFormFieldModule,
  MatInputModule, MatSortModule, MatCheckboxModule, MatIconModule, MatMenuModule,
  MatCardModule, MatStepperModule, MatExpansionModule, MatDatepickerModule,
  MatChipsModule, MatTabsModule, MatSelectModule, MatRadioModule
];
const BootstrapModules = [
  NgbTabsetModule, NgbPopoverModule, NgbAlertModule
];

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    CoreModule,
    ReactiveFormsModule,
    ...MaterialModules,
    ...BootstrapModules,
    LoaderModule,
    UserManagementRoutingModule,
    FormModule
  ],
  declarations: [
    UserManagementComponent,
    ConfirmRegistrationComponent,
    AccountRecoveryComponent,
    UserAccountComponent,
    ChangePasswordComponent,
    ResetPasswordComponent,
    PersonalInfoComponent,
  ],
  providers: [
    UserManagementService,
  ],
  entryComponents: []
})

export class UserManagementModule {
}
